/* eslint-disable quotes */
import Vue from 'vue'
import VueRouter from 'vue-router'

import Signin from '@/components/Signin'
import ServiceList from '@/components/services/ServiceList'
import ServiceView from '@/components/services/ServiceView'
import Home from '@/views/Home'
import Contact from '@/views/Contact'
import Pricing from '@/views/Pricing'
import Policy from '@/views/Policy'
import Cookies from '@/views/Cookies'
import About from '@/views/About'
import Information from '@/views/Information'
import NotFoundComponent from '@/views/404'

Vue.use(VueRouter)


const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    meta: {
      title: 'Strona Gwna | Ozon Expert',
      metaTags: [
        {
          name: 'description',
          content: 'Kompleksowa likwidacja bakterii, pleni, roztoczy, zarodnikw grzybw, wirusw i alergenw dla firm i osb prywatnych na terenie wojewdztwa pomorskiego.'
        },
        {
          property: 'og:description',
          content: 'Kompleksowa likwidacja bakterii, pleni, roztoczy, zarodnikw grzybw, wirusw i alergenw dla firm i osb prywatnych na terenie wojewdztwa pomorskiego.'
        }
      ]
    }
  },
  {
    path: '/kontakt',
    name: 'Contact',
    component: Contact,
    meta: {
      title: 'Kontakt | Ozon Expert',
      metaTags: [
        {
          name: 'description',
          content: 'Zadzwo i umw si na spotkanie! Staramy si odpowiada na wszystkie zgoszenia w cigu 24 godzin. Najczciej robimy to nawet szybciej.'
        },
        {
          property: 'og:description',
          content: 'Zadzwo i umw si na spotkanie! Staramy si odpowiada na wszystkie zgoszenia w cigu 24 godzin. Najczciej robimy to nawet szybciej.'
        }
      ]
    }
  },
  {
    path: '/cennik',
    name: 'Pricing',
    component: Pricing,
    meta: {
      title: 'Cennik | Ozon Expert',
      metaTags: [
        {
          name: 'description',
          content: 'Sprawd, jakie usugi oferujemy. Zamw ozonowanie i ciesz si czystym, wieym powietrzem. Staramy si indywidualnie podchodzi do potrzeb kadego z naszych klientw. Dlatego nie mamy cile ustalonego cennika usug. Wycena jest zawsze indywidualna, sporzdzamy j po przeprowadzeniu bezpatnej konsultacji u klienta. '
        },
        {
          property: 'og:description',
          content: 'Sprawd, jakie usugi oferujemy. Zamw ozonowanie i ciesz si czystym, wieym powietrzem. Staramy si indywidualnie podchodzi do potrzeb kadego z naszych klientw. Dlatego nie mamy cile ustalonego cennika usug. Wycena jest zawsze indywidualna, sporzdzamy j po przeprowadzeniu bezpatnej konsultacji u klienta. '
        }
      ]
    }
  },
  {
    path: '/onas',
    name: 'About',
    component: About,
    meta: {
      title: 'O Nas | Ozon Expert',
      metaTags: [
        {
          name: 'description',
          content: 'Mamy wieloletnie dowiadczenie w ozonowaniu i dezynfekcji, dlatego nasze usugi kierujemy zarwno do klientw indywidualnych, jak i firm. Kade zlecenie jest dla nas najwaniejsze, dlatego do kadego klienta podchodzimy w peni indywidualnie. '
        },
        {
          property: 'og:description',
          content: 'Mamy wieloletnie dowiadczenie w ozonowaniu i dezynfekcji, dlatego nasze usugi kierujemy zarwno do klientw indywidualnych, jak i firm. Kade zlecenie jest dla nas najwaniejsze, dlatego do kadego klienta podchodzimy w peni indywidualnie. '
        }
      ]
    }
  },
  {
    path: '/admin',
    name: 'Signin',
    component: Signin,
    meta: {
      title: 'Logowanie | Ozon Expert'
    }
  },
  {
    path: '/uslugi',
    name: 'services',
    component: ServiceList,
    meta: {
      title: 'Usugi | Ozon Expert',
      metaTags: [
        {
          name: 'description',
          content: 'Sprawd zakres naszych usug  razem szybko i bezstresowo rozwiemy Twj problem. Przekonaj si sam, jak moe poprawi si jako ycia po przeprowadzeniu zabiegu ozonowania. Umw si na konsultacj ju dzi. '
        },
        {
          property: 'og:description',
          content: 'Sprawd zakres naszych usug  razem szybko i bezstresowo rozwiemy Twj problem. Przekonaj si sam, jak moe poprawi si jako ycia po przeprowadzeniu zabiegu ozonowania. Umw si na konsultacj ju dzi. '
        }
      ]
    }
  },
  {
    path: '/informacje',
    name: 'information',
    component: Information,
    meta: {
      title: 'Informacje | Ozon Expert',
      metaTags: [
        {
          name: 'description',
          content: 'O ozonowaniu wiemy wszystko. Przeprowadzimy Ci przez bezpieczne ozonowanie dowolnego obiektu. Zajmujemy si profesjonalnym czyszczeniem pomieszcze, samochodw i obiektw.'
        },
        {
          property: 'og:description',
          content: 'O ozonowaniu wiemy wszystko. Przeprowadzimy Ci przez bezpieczne ozonowanie dowolnego obiektu. Zajmujemy si profesjonalnym czyszczeniem pomieszcze, samochodw i obiektw.'
        }
      ]
    }
  },
  {
    path: '/uslugi/:id',
    name: 'service',
    props: true,
    component: ServiceView,
    meta: {
      title: 'Usugi | Ozon Expert'
    }
  },
  {
    path: '/polityka',
    name: 'policy',
    component: Policy
  },
  {
    path: '/ciasteczka',
    name: 'cookies',
    component: Cookies
  },
  {
    path: '*',
    component: NotFoundComponent,
    meta: {
      title: '404 | Ozon Expert'
    }
  }
]

const router = new VueRouter({
  routes,
  mode: 'history',
  scrollBehavior(to) {
    if (to.hash) {
      return window.scrollTo({ top: document.querySelector(to.hash).offsetTop, behavior: 'smooth' })
    } else {
      return { x: 0, y: 0 }
    }

  }
})

// This callback runs before every route change, including on page load.
router.beforeEach((to, from, next) => {
  // This goes through the matched routes from last to first, finding the closest route with a title.
  // eg. if we have /some/deep/nested/route and /some, /deep, and /nested have titles, nested's will be chosen.
  const nearestWithTitle = to.matched.slice().reverse().find(r => r.meta && r.meta.title)

  // Find the nearest route element with meta tags.
  const nearestWithMeta = to.matched.slice().reverse().find(r => r.meta && r.meta.metaTags)

  // If a route with a title was found, set the document (page) title to that value.
  if (nearestWithTitle) document.title = nearestWithTitle.meta.title

  // Remove any stale meta tags from the document using the key attribute we set below.
  Array.from(document.querySelectorAll('[data-vue-router-controlled]')).map(el => el.parentNode.removeChild(el))

  // Skip rendering meta tags if there are none.
  if (!nearestWithMeta) return next()

  // Turn the meta tag definitions into actual elements in the head.
  nearestWithMeta.meta.metaTags.map(tagDef => {
    const tag = document.createElement('meta')

    Object.keys(tagDef).forEach(key => {
      tag.setAttribute(key, tagDef[key])
    })

    // We use this to track which meta tags we create, so we don't interfere with other ones.
    tag.setAttribute('data-vue-router-controlled', '')

    return tag
  })
    // Add the meta tags to the document head.
    .forEach(tag => document.head.appendChild(tag))

  next()
})


export default router
